@import url(https://fonts.googleapis.com/css?family=PT+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}




.shield {
  width: 75px;
  height: 75px;
}

@media screen and (min-width : 300px)
{
 .shield
  {
    width: 125px;
    height: 125px;
  }
}

@media screen and (min-width : 450px)
{
 .shield
  {
    width: 160px;
    height: 160px;
  }
}

@media screen and (min-width : 600px)
{
 .shield
  {
    width: 200px;
    height: 200px;
  }
}
.team-score {
  font-size: 50px;
}


@media screen and (min-width : 300px)
{
  .team-score {
    font-size: 1.5em;
  }
}

@media screen and (min-width : 450px)
{
  .team-score {
    font-size: 2.25em;
  }
}

@media screen and (min-width : 600px)
{
  .team-score {
    font-size: 3em;
  }
}
.social-network {
  font-size: 1.3em;
  text-align: center;
}

.social-icon {
  height: 35px;
  width: 35px;
  padding: 5px;
  cursor: pointer;
}
* {
  font-family: 'PT Sans', sans-serif
}

.versus-indicator {
  font-weight: bolder;
  margin-top: -50px;
}

@media screen and (min-width : 300px)
{
 .page-title
  {
    font-size: 1em;
  }

  .versus-indicator {
    font-size: 1em;
  }
}

@media screen and (min-width : 450px)
{
 .page-title
  {
    font-size: 1.5em; 
  }

  .versus-indicator {
    font-size: 2em;
  }
}

@media screen and (min-width : 600px)
{
  .page-title
  {
    font-size: 2em;
  }

  .versus-indicator {
    font-size: 3em;
  }

}

