.team-score {
  font-size: 50px;
}


@media screen and (min-width : 300px)
{
  .team-score {
    font-size: 1.5em;
  }
}

@media screen and (min-width : 450px)
{
  .team-score {
    font-size: 2.25em;
  }
}

@media screen and (min-width : 600px)
{
  .team-score {
    font-size: 3em;
  }
}