@import url('https://fonts.googleapis.com/css?family=PT+Sans');

* {
  font-family: 'PT Sans', sans-serif
}

.versus-indicator {
  font-weight: bolder;
  margin-top: -50px;
}

@media screen and (min-width : 300px)
{
 .page-title
  {
    font-size: 1em;
  }

  .versus-indicator {
    font-size: 1em;
  }
}

@media screen and (min-width : 450px)
{
 .page-title
  {
    font-size: 1.5em; 
  }

  .versus-indicator {
    font-size: 2em;
  }
}

@media screen and (min-width : 600px)
{
  .page-title
  {
    font-size: 2em;
  }

  .versus-indicator {
    font-size: 3em;
  }

}
