

.shield {
  width: 75px;
  height: 75px;
}

@media screen and (min-width : 300px)
{
 .shield
  {
    width: 125px;
    height: 125px;
  }
}

@media screen and (min-width : 450px)
{
 .shield
  {
    width: 160px;
    height: 160px;
  }
}

@media screen and (min-width : 600px)
{
 .shield
  {
    width: 200px;
    height: 200px;
  }
}